import React, {Component} from 'react';
import s from './Header.module.css'
import {NavLink} from "react-router-dom";

const Header = (props) => {
    return (
        <header className={s.header}>
            <img src='https://seeklogo.com/images/B/Boba_Fett-logo-269341E5B6-seeklogo.com.png'/>
            <div className={s.loginBlock}>
                {props.isAuth
                    ? <div className={s.NavLink}>{props.login} - <button onClick={props.logout}>Log out</button></div>
                    : <div><NavLink className={s.NavLink} to={'/login'}>Login</NavLink></div>}
            </div>
        </header>
    )
}

export default Header;